@import url(./app.css);
@import url(./quill.css);
@import url(./v-accordion.css);
label {
  margin: 0;
  padding: 0;
  display: inline-block; }

select,
input[type="text"] {
  height: 30px; }

p {
  margin: 0;
  color: #6e6e6e;
  font-size: 16px; }
  @media only screen and (max-width: 699px) and (orientation: portrait) {
    p {
      font-size: 12px; } }
  @media only screen and (max-width: 699px) and (orientation: landscape) {
    p {
      font-size: 12px; } }

body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0; }

button, select {
  cursor: pointer; }

* {
  outline: none; }

select {
  border: 0;
  /*Removes border*/
  appearance: none;
  -webkit-appearance: none;
  /*Removes default chrome and safari style*/
  -moz-appearance: none;
  /*Removes default style Firefox*/
  -o-appearance: none;
  /*Remove defautl style pre-webkit Opera*/
  -ms-appearance: none;
  /*Remove defautl style of Ms browser*/
  background-color: transparent;
  border: 1px solid #dddddd;
  color: #6e6e6e;
  padding-left: 5px;
  background: url("/app/images/dropdown-arrow.png");
  background-position: right center;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  cursor: pointer; }

select::-ms-expand {
  display: none; }

input::-webkit-calendar-picker-indicator {
  background-color: inherit; }

input[type=number] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */ }

input[type=range] {
  -webkit-appearance: none;
  cursor: pointer; }

input[type=range]:focus {
  outline: none; }

input[type=range]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  background-color: #e0e0e0;
  height: 3px; }

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  margin-top: -7px;
  border-radius: 50%;
  background-color: #106cc8;
  box-shadow: 0px 5px 10px -2px rgba(135, 135, 135, 0.5);
  cursor: pointer; }

input[type=range]::-moz-range-track {
  -webkit-appearance: none;
  background-color: #e0e0e0;
  height: 3px; }

input[type=range]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  margin-top: -7px;
  border-radius: 50%;
  background-color: #106cc8;
  box-shadow: 0px 5px 10px -2px rgba(135, 135, 135, 0.5);
  cursor: pointer; }

input[type=range]::-ms-track {
  -webkit-appearance: none;
  background: transparent;
  border-color: transparent;
  color: transparent;
  border-width: 5px 0;
  height: 3px; }

input[type=range]::-ms-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  box-shadow: 0 2px 10px 0px #106cc8;
  cursor: pointer; }

input[type=range]::-ms-fill-lower {
  background: #e0e0e0;
  border-radius: 10px; }

input[type=range]::-ms-fill-upper {
  background: #e0e0e0;
  border-radius: 10px; }

input[type=number] {
  width: 100%; }

div[data-tooltip] {
  position: relative;
  z-index: 2; }

div[data-tooltip]::before {
  content: attr(data-tooltip);
  position: absolute;
  background-color: #106cc8;
  color: #fff;
  display: block;
  font-size: 13px;
  padding: 5px;
  border-radius: 5px;
  white-space: nowrap;
  text-decoration: none;
  visibility: hidden;
  opacity: 0; }

div[data-tooltip]:hover::before {
  display: block;
  visibility: visible;
  opacity: 0.85;
  animation: autoHide 0.5s linear forwards;
  animation-delay: 1.5s; }

@keyframes autoHide {
  100% {
    opacity: 0;
    visibility: hidden;
    display: none; } }

/** positioning **/
div[data-tooltip][data-placement="left"]::before {
  top: 20%;
  right: 100%;
  margin-right: 10px; }

.desktop-only {
  display: flex; }

.mobile-only {
  display: none; }

.mobile-land-only {
  display: none; }

.hide {
  display: none; }

.show {
  display: block; }

.bolded {
  background-color: #106cc8;
  color: white; }

.unBoled {
  background-color: rgba(255, 0, 255, 0);
  color: #106cc8; }

.mobile-land-size {
  width: 100%;
  height: auto;
  align-self: flex-end; }

@media only screen and (max-width: 699px) and (orientation: landscape) {
  .break-word {
    display: block; } }



