@charset "UTF-8";
/*
レイアウト用
*/
.nomargin {
  margin: 0 0; }

.notopmargin {
  margin-top: 0; }

.nobottommargin {
  margin-bottom: 0; }

.nopadding {
  padding: 0 0; }

.width100 {
  width: 100%; }

.height100,
.height100 md-tab-content > div {
  height: 100%;
  box-sizing: border-box; }

.offset {
  margin-left: 1em; }

.offset.line {
  border-left: solid 2px #f0f0f0; }

.padding-all {
  padding: 0.5em; }

.block {
  display: block; }

/*animation*/
.animated {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease; }

.animated.ng-enter {
  opacity: 0; }

.animated.ng-enter.ng-enter-active {
  opacity: 1; }

.animated.ng-leave {
  opacity: 1; }

.animated.ng-leave.ng-leave-active {
  opacity: 0; }

.animated.ng-move {
  opacity: .5; }

.animated.ng-move.ng-move-active {
  opacity: 1; }

.height64.animated {
  overflow: hidden; }

.height64.animated.ng-enter,
.height64.animated.ng-leave.ng-leave-active {
  height: 0;
  max-height: 0;
  min-height: 0; }

.height64.animated.ng-enter.ng-enter-active,
.height64.animated.ng-leave {
  height: 64px;
  max-height: 64px;
  min-height: 64px; }

/*
textareaのマージン無し版
*/
md-input-container.nomargin {
  margin-top: 18px;
  margin-bottom: 0px; }

md-input-container.nomargin .md-errors-spacer {
  min-height: 0px; }

md-input-container.nomargin.no-label {
  margin-top: 0; }

/*
md-grid-tile
*/
md-grid-tile.image {
  background-position: center center;
  background-repeat: no-repeat;
  /*background-attachment: fixed;*/
  background-size: auto 100%;
  background-color: #464646; }

md-dialog.md-dialog-fullscreen {
  max-width: 100%;
  max-height: 100%; }

/*
username
*/
.username {
  margin: 0 0; }

.username > span:first-child {
  font-weight: bolder; }

.username > span {
  color: gray; }

/*
arrow-box
*/
.arrow-box {
  position: relative;
  padding: 0.5em 0.5em;
  border-radius: 4px;
  border: none; }

.arrow-box:after {
  top: 12px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: transparent;
  border-width: 8px;
  margin-top: -8px; }

.arrow-box.left:after {
  right: 100%;
  border-right-color: inherit; }

.arrow-box.right:after {
  left: 100%;
  border-left-color: inherit; }

.overlay-base {
  position: relative; }

.overlay {
  position: absolute;
  z-index: 1;
  /*opacity: 0.75;*/
  top: -0.5em;
  right: 0;
  margin: auto;
  -webkit-transition: all ease 0.4s;
  -moz-transition: all ease 0.4s;
  -o-transition: all ease 0.4s;
  transition: all ease 0.4s; }

.overlay.bottom {
  top: auto;
  bottom: -0.5em; }



